___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: #333;
  --text-color: #333;
  --bg-color: #f4f4f4;
  --shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  --radius: 8px;
  --spacing: 1rem;
  --max-width: 1200px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
  font-family: system-ui, sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
}

/* Layout base */
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}


header,
footer {
  background-color: var(--primary-color);
  color: white;
  padding: var(--spacing);
  text-align: center;
  flex-shrink: 0;
  box-shadow: var(--shadow);
}


#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

#main {
  flex: 1;
  padding: 2rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  max-width: var(--max-width);
  margin: 0 auto;
}


#main button,
#main input {
  max-width: 100%;
  width: min(400px, 90%);
  margin: 0 auto;
  height: 3em;
  border: none;
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  padding: 0.5em 1em;
}


#main img {
  max-width: 100%;
  height: auto;
  display: block;
  box-shadow: var(--shadow);
  border-radius: var(--radius);
}

#gifContainer {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  justify-content: center;
  text-align: center;
}

#gifContainer h2 {
  font-size: clamp(1.25rem, 2vw, 2rem);
}

@media (min-width: 768px) {
  #main {
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
  }

  #gifContainer {
    flex: 1 1 300px;
    text-align: left;
  }
}
